:root {
  --box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  --main-font: 'Roboto', sans-serif;
  --column-color: #f00;
  --font-size: clamp(0.625rem, 0.4643rem + 0.8571vw, 1rem);
}

.theme-light {
  --bg-color: #fff;
  --text-color: #27313c;
  --accent-color: #dde8f6;
  --hover-color: #aac1dc;
  --github-logo: #24292f;
}

.theme-dark {
  --bg-color: #202020;
  --text-color: #dcdfe5;
  --accent-color: #26313d;
  --hover-color: #000;
  --github-logo: #fff;
}
